.grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    gap: 20px;
}

.flex {
    display: flex;
    flex-flow: row wrap;
    justify-content: center;
}

